import React from 'react';
import Headroom from 'react-headroom';
import { Link } from 'gatsby';

import 'bulma/css/bulma.css';
import './NavBar.css';

const NavBar = () => {
  return (
    <>
      <nav className="navbar is-transparent">
        <div className="navbar-brand">
          <label className="title navbar-item has-text-weight-light">
            ThanMatt
        </label>
        </div>
      </nav>
      <Headroom>
        <nav className="navbar is-transparent">
          <div className="navbar-brand">
            <Link to="/" activeStyle={{ color: 'blue' }} className="navbar-item is-right has-text-weight-light">
              Home
          </Link>
            <Link to="/blog" activeStyle={{ color: 'blue' }} className="navbar-item is-right has-text-weight-light">
              Blog
          </Link>
            <a className="navbar-item is-right has-text-weight-light">
              Photos
          </a>
            <a className="navbar-item is-right has-text-weight-light">
              Archive
          </a>
          </div>

        </nav>

      </Headroom>
    </>

  )
}

export default NavBar;